<script>
import {reactive} from "vue";

let loc = window.location
let new_uri = "";
if (loc.protocol === "https:") {
  new_uri = "wss:";
} else {
  new_uri = "ws:";
}
new_uri += "//" + loc.host;
export default {
  mounted() {
    this.init()
  },
  data() {
    return {
      size: 14,
      cls: '',
      ws: null,
      enable: false,
      matchWaiting: false
    }
  },
  methods: {
    admitDefeat() {
      if (!this.enable) {
        alert("不是你的回合!")
        return
      }
      this.ws.send("[surrender]");
    },
    init() {
      this.cls = [];
      let c = this.cls;
      for (let i = 1; i <= this.size + 1; i++) {
        c[i] = [];
        for (let j = 1; j <= this.size + 1; j++) {
          c[i][j] = reactive({
            w: false,
            b: false,
            preview: false,
            kiko: true
          });
        }
      }
    },
    repentance() {
      if (!this.enable) {
        alert("不是你的回合!")
        return
      }
      this.ws.send("[repentance]");
      this.enable = false;
    },
    fallingChild(x, y) {
      if (this.enable) {
        // this.$refs.audio.play();
        this.ws.send((x - 1) + ',' + (y - 1));
        this.enable = false;
      }
    },
    mouseleave(x, y) {
      if (this.cls[x][y].w) {
        return
      } else if (this.cls[x][y].b) {
        return
      } else {
        this.cls[x][y].preview = false;
      }
    },
    // eslint-disable-next-line no-unused-vars
    mouseover(x, y) {
      if (this.cls[x][y].w) {
        return
      } else if (this.cls[x][y].b) {
        return
      } else {
        this.cls[x][y].preview = true;
      }
    }
    ,
    startOnline() {
      if (this.ws != null) {
        alert("已经在匹配中")
        return
      }
      this.ws = new WebSocket( new_uri+ "/startOnline");
      let w = this.ws;
      this.matchWaiting = true;
      w.onmessage = (event) => {
        console.log(event.data);
        if (event.data === "[win]") {
          alert("win");
        } else if (event.data === "[lose]") {
          alert("lose");
        } else if (event.data === "[yourTurn]") {
          this.matchWaiting = false;
          console.log("ok")
          this.enable = true;
        } else if (event.data.startsWith("[repentance]")) {
          let data = event.data.split(",");
          let x1 = Number(data[1]) + 1;
          let y1 = Number(data[2]) + 1;
          let x2 = Number(data[3]) + 1;
          let y2 = Number(data[4]) + 1;
          console.log(x1, y1, x2, y2)
          this.cls[x1][y1].w = false;
          this.cls[x1][y1].b = false;
          this.cls[x2][y2].w = false;
          this.cls[x2][y2].b = false;
        } else {
          let data = JSON.parse(event.data);
          let x = data.x + 1;
          let y = data.y + 1;
          console.log(data)
          if (data.color == "0") {
            this.cls[x][y].w = true;
          } else {
            this.cls[x][y].b = true;
          }
        }
        w.onclose = () => {
          this.init()
          this.ws = null;
          this.enable = false;
          this.matchWaiting = false;
        }

      }

    }
  }
}
</script>

<template>
  <div>
    <div style="height: 50px">
      <span v-show="matchWaiting" style="color: white">匹配中</span>
      <span v-show="enable" style="color: white">请你执棋</span>
    </div>
    <div class="main">
      <table id="drops">
        <tr v-for="i in size+1" :key="i">
          <td v-for="j in size+1" :key="j" @click="fallingChild(i,j)" @mouseleave="mouseleave(i,j)"
              @mouseover="mouseover(i,j)">
            <div :class="cls[i][j]"></div>
          </td>
        </tr>
      </table>
      <table id="board" border="1" cellspacing="0" cellpadding="0">
        <tr v-for="i in size" :key="i">
          <td v-for="j in size" :key="j">
          </td>
        </tr>
      </table>
      <button @click="startOnline">匹配</button>
      <button @click="admitDefeat">认输</button>
      <button @click="repentance">悔棋</button>
    </div>
    <audio ref="audio" src="/mp3/"></audio>
  </div>
</template>

<style scoped>
table {
  border-collapse: collapse;
  border-spacing: 0;
  //border: 1px solid black;
}

tr {
  height: 50px;
  //border: 1px solid black;
}

td {
  box-sizing: border-box;
  width: 50px;
  //border: 1px solid black;
}

.main {
  position: relative;
}

.w {
  background-color: white;
}

.b {
  background-color: black;
}

.preview {
  border: red 1px solid;
}

.l {
  display: none;
}

.kiko {
  border-radius: 50%;
  width: 20px;
  height: 20px;
  margin: auto;
}


* {
  padding: 0;
  margin: 0;
  font-size: 12px;
}

#board {
  border-color: yellow;
  position: absolute;
  top: 25px;
  left: 25px;
  z-index: 1;
}

#drops {
  //visibility:hidden;
  position: relative;
  //border: 1px red solid;
  z-index: 3;
}

</style>